//start slide banner
.slide-banner {
  .swiperBanner {
    height: 760px;

    .swiper-wrapper {
      .swiper-slide {
        &:nth-child(1) {
          background: linear-gradient(
                          97.88deg,
                          rgba(0, 0, 0, 0.7) 0%,
                          rgba(0, 0, 0, 0) 55.81%
          ),
          url(".././images/banner-1.jpg") no-repeat center;
          background-size: cover;
        }

        &:nth-child(2) {
          background: linear-gradient(
                          97.88deg,
                          rgba(0, 0, 0, 0.7) 0%,
                          rgba(0, 0, 0, 0) 55.81%
          ),
          url(".././images/banner-2.jpg") no-repeat center;
          background-size: cover;

          .swiperCaption {
            h1 {
              @media (max-width: 767.98px) {
                font-size: 48px;
                line-height: 56px;
                letter-spacing: 2px;
              }
            }
          }
        }

        .swiperCaption {
          padding-top: 90px;
          @media (max-width: 767.98px) {
            text-align: center;
          }

          h1 {
            font-family: 'a Awal Ramadhan', sans-serif;
            color: #0DAB69;
            font-weight: 400;
            font-size: 128px;
            line-height: 100%;
            -webkit-text-stroke: 2px white;
            letter-spacing: 5px;
            @media (max-width: 767.98px) {
              font-size: 72px;
              line-height: 100%;
              letter-spacing: 2px;
            }

            span {
              -webkit-text-stroke: unset;
              letter-spacing: unset;
              font-size: 48px;
              color: #ffffff;
              @media (max-width: 767.98px) {
                font-size: 32px;
                line-height: 36px;
              }
            }
          }

          p {
            margin-top: 30px;
            margin-bottom: 40px;
            width: 620px;
            font-weight: 500;
            font-size: 20px;
            line-height: 28px;
            @media (max-width: 767.98px) {
              margin-top: 5px;
              margin-bottom: 30px;
              font-size: 16px;
              line-height: 24px;
              width: 100%;
            }
          }

          a {
            margin-left: 0;
            @media (max-width: 767.98px) {
              height: 56px;
              padding: 0 44px;
              margin: auto;
            }
          }
        }
      }
    }

    .swiper-pagination {
      right: 90px;
      @media (max-width: 1199.98px) {
        right: 60px;
      }
      @media (max-width: 767.98px) {
        bottom: 110px;
      }

      .swiper-pagination-bullet {
        user-select: none;
        position: relative;
        background: none;
        opacity: 1;
        width: 32px;
        height: 32px;
        margin: var(--swiper-pagination-bullet-vertical-gap, 30px) 0;
        @media (max-width: 767.98px) {
          margin: 0 12px 30px;
        }

        &:nth-child(1) {
          &::before {
            position: absolute;
            content: '';
            width: 1px;
            height: 330px;
            background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
            bottom: -30px;
            z-index: -1;
            @media (max-width: 1199.98px) {
              height: 200px;
            }
            @media (max-width: 767.98px) {
              height: 1px;
              width: 276px;
              top: 15px;
              right: -36px;
              background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
            }
          }
        }

        &:nth-child(2) {
          &::before {
            position: absolute;
            content: '';
            width: 1px;
            height: 268px;
            z-index: -1;
            background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
            @media (max-width: 1199.98px) {
              height: 155px;
            }
            @media (max-width: 767.98px) {
              height: 1px;
              width: 220px;
              top: 15px;
              left: 16px;
              bottom: 0;
              right: 0;
              background: linear-gradient(90deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
            }
          }
        }

        &::after {
          content: "";
          position: absolute;
          background: white;
          width: 12px;
          height: 12px;
          border-radius: 50%;
          top: 50%;
          transform: translateY(-50%);
          bottom: auto;
          left: 0;
          right: 0;
          margin: auto;
          box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
        }
      }

      .swiper-pagination-bullet-active {
        background: #8e8e8e;

      }
    }

    .card-items {
      position: absolute;
      bottom: 0;
      z-index: 1;
      width: 100%;
      background: linear-gradient(180deg, rgba(255, 246, 224, 0) 0%, rgba(255, 246, 224, 0.1) 16.15%, rgba(255, 246, 224, 0.5) 51.56%, rgba(255, 246, 224, 0.95) 83.85%, #FFF6E0 100%);
      display: flex;
      padding: 10px;
      justify-content: center;
      align-items: center;
      gap: 20px;
      text-align: center;
      @media (max-width: 767.98px) {
        gap: 5px;
      }

      .item {
        width: 222px;
        background: linear-gradient(to bottom, rgba(164, 92, 30, 1), rgba(164, 92, 30, 0));
        box-sizing: border-box;
        border-radius: 12px 12px 0 0;
        padding: 2px;
        cursor: pointer;
        height: 140px;
        @media (max-width: 991.98px) {
          height: 106px;
          width: 85px;
        }

        .item-inner {
          width: 100%;
          height: 100%;
          border-radius: 12px 12px 0 0;
          background: #FFF6E0;
          padding: 30px 28px;

          p {
            color: #262626;
            font-weight: 700;
            font-size: 1.8rem;
            line-height: 26px;
            margin-top: 10px;
          }

          @media (max-width: 991.98px) {
            padding: 15px 7px;
            img {
              width: 36px;
            }
            p {
              font-size: 12px;
              line-height: 16px;
            }
          }

        }

        &:hover {
          background: #a45c1e;
          box-shadow: 0 6px 10px rgba(0, 0, 0, 0.2);
          border-radius: 12px;

          .item-inner {
            border-radius: 12px;
          }
        }


      }
    }

    .swiper-button-next {
      padding: 30px 34px;
      top: auto;
      bottom: 0;
      left: auto;
      right: 0;
      background: linear-gradient(180deg, rgba(255, 246, 224, 0.7) 0%, rgba(255, 246, 224, 0) 100%);
      @media (max-width: 1199.98px) {
        display: none;
      }

      &:after {
        font-size: 20px;
        font-weight: 600;
        color: #ffb87a;
        transform: rotate(90deg);
      }
    }

    .swiper-button-prev {
      padding: 30px 34px;
      background: linear-gradient(180deg, rgba(255, 246, 224, .7) 0, rgb(255 246 224 / 64%) 100%);
      border-radius: 8px 8px 0 0;
      top: auto;
      bottom: 60px;
      left: auto;
      right: 0;
      @media (max-width: 1199.98px) {
        display: none;
      }

      &:after {
        font-size: 20px;
        font-weight: 600;
        color: #ffb87a;
        transform: rotate(90deg);
      }
    }

    .swiper-button-disabled {
      opacity: 1;

      &:after {
        opacity: 0.35;
      }
    }
  }
}

//end slide banner

