//start section beauty
.section-beauty {
  margin-top: -230px;
  z-index: 2;
  position: relative;

  .beauty-bg {
    background: #FCECC4 url("../images/bg-beauty.png") no-repeat center;
    background-size: cover;
    @media (max-width: 1200px) {
      background: #FCECC4 url("../images/bg-beauty.png") no-repeat center;
      background-size: cover;
    }

    .swiperBeauty {
      height: 800px;
      @media (max-width: 991.98px) {
        height: 100%;
        padding-bottom: 143px;
      }

      .swiper-slide {
        .slide-content {
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 120px;
          margin-top: 40px;
          @media (max-width: 1199.98px) {
            gap: 60px;
            .content {
              width: 50%;
            }
            .image {
              //width: 50%;

              img {
                //width: 100%;
              }
            }
          }
          @media (max-width: 991.98px) {
            flex-wrap: wrap;
            flex-direction: column-reverse;
            gap: 15px;
            margin: 40px -15px 0;
            .content {
              width: 100%;
            }
            .image {
              width: unset;

              img {
                width: 100%;
              }
            }
          }

          .content {
            h3 {
              font-weight: 700;
              font-size: 3.2rem;
              line-height: 40px;
              letter-spacing: -0.01em;
              color: #703A0C;
              position: relative;
              margin-bottom: 40px;

              &:before {
                position: absolute;
                content: '';
                width: 655px;
                height: 1px;
                top: 60px;
                background: linear-gradient(89.14deg, rgba(164, 92, 30, 0) 1.17%, #A45C1E 52.5%, rgba(164, 92, 30, 0) 99.72%);
                @media (max-width: 1199.98px) {
                  width: 315px;
                }
              }
            }

            @media (max-width: 1199.98px) {
              .txt-content {
                font-size: 14px;
                line-height: 20px;
              }
            }
          }

          .image {
            position: relative;
            @media (max-width: 991.98px) {
              text-align: center;
            }

            img {
              border-radius: 240px 240px 0 0;
            }

            .overlay {
              top: 0;
              width: 100%;
              height: 100%;
              position: absolute;
              background: linear-gradient(180deg, rgba(252, 236, 196, 0) 50%, rgba(252, 236, 196, 0.95) 90.62%, #FCECC4 100%);
            }
          }
        }
      }

      .swiper-pagination {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 208px;
        bottom: 100px;
        @media (max-width: 991.98px) {
          bottom: 40px;
          gap: 130px;
        }
        @media (max-width: 767.98px) {
          width: 100%!important;
          gap: 70px;
          padding-top: 25px;
        }

        &:after {
          content: "";
          width: 1000px;
          height: 0;
          position: absolute;
          border-bottom: 3px dotted #A45C1E;
          opacity: 0.7;
          left: 0;
          right: 0;
          margin: auto;
          z-index: -1;
          @media (max-width: 991.98px) {
            width: 750px;
          }
          @media (max-width: 767.98px) {
            width: unset;
            border-bottom: 2px dotted #a45c1e;
          }
        }
      }

      .swiper-pagination-bullet {
        background: #A45C1E;
        width: 16px;
        height: 16px;
        transition: .1s;
        opacity: 1;
        position: relative;
        @media (max-width: 991.98px) {
          width: 12px;
          height: 12px;
        }

        &:before {
          position: absolute;
          font-weight: 700;
          font-size: 20px;
          color: #AC9F94;
          top: -30px;
          width: 148px;
          left: -59px;
          transition: .1s;
          @media (max-width: 991.98px) {
            line-height: 22px;
            font-size: 14px;
          }
        }

        &:nth-child(1) {
          &:before {
            content: 'Jeddah City';
          }
        }

        &:nth-child(2) {
          &:before {
            content: 'Riyadh City';
          }
        }

        &:nth-child(3) {
          &:before {
            content: 'Al Khobar City';
            width: 180px;
            left: -78px;
          }
        }

        &:nth-child(4) {
          &:before {
            content: 'Mecca City';
          }
        }
      }

      .swiper-pagination-bullet-active {
        transition: .1s;
        width: 28px;
        height: 28px;
        border: 6px solid #ffffff70;

        &:before {
          font-size: 24px;
          color: #A45C1E;
          transition: .1s;
          @media (max-width: 991.98px) {
            font-size: 16px;
            line-height: 24px;
          }

        }
      }

      .swiper-button-next, .swiper-button-prev {
        background: #b4945c8f;
        border-radius: 50%;
        width: 56px;
        height: 56px;
        @media (max-width: 1515px) {
          display: none;
        }

        &:after {
          color: #7e654f;
          font-size: 20px;
          font-weight: 700;
        }
      }
    }
  }

  .container-cus {
    max-width: 1440px;
  }

  .applyNow-bg {
    background: linear-gradient(90deg, #093120 0%, #205A42 50%);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15), 0 4px 16px rgba(0, 0, 0, 0.15);
    @media (max-width: 991.98px) {
      margin: 0 -15px;
    }

    .applyNow-bg-2 {
      background: url("../images/bg-your-all-in-one.png") no-repeat;
      background-size: contain;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-left: 120px;
      @media (max-width: 991.98px) {
        flex-wrap: wrap;
        flex-direction: column-reverse;
        padding: 0;
        background: url(../images/bg-your-all-in-one-mb.png) no-repeat;
        background-size: 100%;
      }

      .applyNow-content {
        padding-right: 80px;
        @media (max-width: 991.98px) {
          padding: 30px 20px 80px;
        }
      }

      h3 {
        font-weight: 700;
        font-size: 56px;
        line-height: 64px;
        margin-bottom: 30px;
        @media (max-width: 1300px) {
          font-size: 3.6rem;
          margin-bottom: 20px;
          line-height: 44px;
        }
      }

      p {
        margin-bottom: 30px;
        color: #ffffff;
        @media (max-width: 1300px) {
          font-size: 16px;
          line-height: 24px;
          margin-bottom: 20px;
        }
      }

      a {
        background: linear-gradient(270deg, #D99F60 0%, #FAC489 53.13%, #D99F60 100%), linear-gradient(270deg, #DAB05A 0%, #E1BF79 51.56%, #DAB05A 100%), linear-gradient(270deg, #FAC489 0%, #EDAB64 53.65%, #FAC489 100%);
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
        @media (max-width: 991.98px) {
          height: 56px;
          padding: 0 44px;
        }
      }

      img {
        width: 50%;
        @media (max-width: 991.98px) {
          width: 100%;
        }
      }
    }
  }
}

//end section beauty
//start section top4
.section-top4 {
  background: url("../images/bg-top4.png") center no-repeat;
  background-size: cover;
  padding-top: 250px;
  margin-top: -80px;
  position: relative;
  z-index: 1;
  @media (max-width: 991.98px) {
    padding-top: 200px;
  }

  .container-cus {
    max-width: 1920px;
    padding: 0;
  }

  .section-top4__body {
    margin: 60px auto auto 0;
    max-width: 1680px;
    display: flex;
    align-items: center;

    @media (max-width: 1199.98px) {
      flex-wrap: wrap;
    }

    .section-top4__body--img {
      overflow: hidden;

      @media (max-width: 1199.98px) {
        width: 100%;
        img {
          width: 100%;
        }
      }

      img[aria-expanded=false] {
        display: none
      }

      img[aria-expanded=true] {
        display: block;
      }
    }

    .section-top4__body--collapse {
      width: 700px;
      height: 720px;
      @media (max-width: 1199.9px) {
        width: 100%;
        height: unset;
      }
      //display img
      .accordion {
        margin-left: -100px;
        height: 100%;
        padding-top: 60px;
        background: linear-gradient(269.66deg, #fac489 .33%, rgba(250, 196, 137, .85) 99.74%);
        @media (max-width: 1399.98px) {
          padding-top: 0;
        }
        @media (max-width: 1198.98px) {
          padding-top: 20px;
        }

        .card {
          border: none;
          background: transparent;

          &:not(:nth-child(1)) {
            .card-header {
              padding-top: 30px;
            }
          }

          .card-body {
            padding: 20px 40px 40px 40px;
            @media (max-width: 1199.98px) {
              padding-left: 140px;
            }
            @media (max-width: 767.98px) {
              padding-left: 115px;
            }
          }
        }

        .card[aria-expanded=true] {
          background: #FFF6E0;
        }

        .card-header {
          background: transparent;
          border: none;
          padding-left: 0;
          padding-bottom: 20px;

          h3 {
            button {
              position: relative;

              &:after {
                position: absolute;
                content: '';
                width: 500px;
                height: 1px;
                @media (max-width: 767.98px) {
                  width: 345px;
                }
              }
            }

            button[aria-expanded=false] {
              padding-left: 120px;
              font-weight: 700;
              font-size: 32px;
              line-height: 40px;
              color: #262626;
              text-decoration: none;
              border: none;
              background: transparent;
              transition: .2s;
              @media (max-width: 1199.98px) {
                padding-left: 170px;
                font-size: 20px;
                line-height: 28px;
              }
              @media (max-width: 767.98px) {
                padding-left: 120px;
              }

              &:after {
                bottom: -19px;
                left: 120px;
                background: linear-gradient(89.14deg, rgba(255, 246, 224, 0) 1.17%, #FFF6E0 52.5%, rgba(255, 246, 224, 0) 99.72%);
                @media (max-width: 1199.98px) {
                  left: 160px;
                }
                @media (max-width: 767.98px) {
                  padding-left: 120px;
                  left: 120px;
                }
              }
            }

            button[aria-expanded=true] {
              font-weight: 700;
              font-size: 32px;
              line-height: 40px;
              text-decoration: none;
              border: none;
              background: transparent;
              padding-left: 40px;
              color: #703A0C;
              transition: .2s;
              @media (max-width: 1199.98px) {
                padding-left: 135px;
                font-size: 20px;
                line-height: 28px;
              }
              @media (max-width: 767.98px) {
                padding-left: 110px;
              }

              &:after {
                background: linear-gradient(89.14deg, rgba(164, 92, 30, 0) 1.17%, #A45C1E 52.5%, rgba(164, 92, 30, 0) 99.72%);
                bottom: -19px;
                left: 40px;
                @media (max-width: 1199.98px) {
                  left: 120px;
                }
              }
            }
          }
        }
      }
    }
  }
}

//end section top4

//start section pick up
.section-pickUp {
  padding: 190px 0 266px;
  background: url("../images/bg-pick-up.png") no-repeat center;
  background-size: cover;
  @media (max-width: 991.98px) {
    padding: 100px 0 220px;
  }

  .container-cus {
    max-width: 1470px;
  }

  h2 {
    margin-bottom: 80px;
  }

  .section-pickUp__body {
    display: flex;
    align-items: center;
    gap: 60px;
    @media (max-width: 991.98px) {
      flex-wrap: wrap;
      justify-content: center;
    }
    @media (max-width: 767.98px) {
      flex-direction: column;
      gap: 20px;
    }

    &--item {

      text-align: center;
      padding: 0 40px;
      font-weight: 400;
      font-size: 1.8rem;
      line-height: 26px;
      background: rgba(255, 246, 224, 0.7);
      height: 360px;
      width: 33.33%;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      @media (max-width: 767.98px) {
        width: 100%;
        height: 270px;
      }

      &--front {
        opacity: 0;
        position: absolute;
        width: 100%;
        height: 100%;
        background: #AA7F5A;
        border: 2px solid #AA7F5A;
        padding: 0 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: .2s;
        @media (max-width: 1199.98px) {
          padding: 0 20px;
          .content {
            font-size: 14px;
            line-height: 20px;
          }
        }

        &:hover {
          opacity: 1;
          transition: .2s;
        }

        p {
          margin-bottom: 30px;
        }

        .line-border-cus {
          width: 16px;
          height: 16px;
          background: #FFF6E0;
          transform: rotate(45deg);
          margin: auto;
          position: relative;

          &:after {
            position: absolute;
            content: "";
            width: 80px;
            height: 1px;
            background: linear-gradient(90deg, #FFF6E0 0.47%, rgba(255, 246, 224, 0) 100.47%);
            transform: rotate(-45deg);
            top: -32px;
          }

          &:before {
            position: absolute;
            content: "";
            width: 80px;
            height: 1px;
            background: linear-gradient(90deg, rgba(255, 246, 224, 0) 0.47%, #FFF6E0 100.47%);
            transform: rotate(-45deg);
            bottom: -33px;
            right: 8px;
          }
        }
      }

      &--back {
        display: block;

        img {
          margin-bottom: 40px;
          @media (max-width: 991.98px) {
            width: 64px;
            margin-bottom: 15px;
          }
        }

        h3 {
          color: #363636;
          font-weight: 700;
          font-size: 24px;
          line-height: 32px;
          @media (max-width: 991.98px) {
            font-size: 20px;
            line-height: 28px;
          }
        }
      }
    }
  }
}


